.list{
    color: #fff;
    width: 100%;
    background-color:#D1C4E9;
    height: 70px;
    margin: 20px auto;
    /* border-radius: 3px; */
    font-weight:300;
    font-size: 24px;
}

.list p{
    padding: 20px;
    position:relative;
}

.list p span{
    position: absolute;
    right: 20px;
    cursor:pointer;
}

.faicons{
    margin: 0 5px;
    /* font-size:10px; */
}

@media (max-width: 600px) {
    .list{
        color: #fff;
        width: 100%;
        background-color:#D1C4E9;
        height: 70px;
        margin: 20px auto;
        /* border-radius: 3px; */
        font-weight:300;
        font-size: 24px;
    }
    
    .list p{
        padding: 20px;
        position:relative;
    }
    
    .list p span{
        position: absolute;
        right: 20px;
        cursor:pointer;
    }
    
    .faicons{
        margin: 0 5px;
        /* font-size:10px; */
    }
    
}
