body{
  background-color: #37474F;
}

.App{
  background-color: #263238;
  min-height:600px;
  width:25%;
  margin:20px auto;
  /* border-radius: 4px; */
}

#todoform input{
  background-color: #ECEFF1;
  border:0;
  width:82%;
  height:70px;
  padding: 0 20px;
  margin: 29px;
  font-size:20px;
  /* border-radius: 4px; */
  color: #263238;
}

#todoform button{
  height:70px;
  width:22%;
  /* border-radius: 5px; */
  border:0;
  background-color: #34ace0;
  cursor:pointer;
  font-weight: bolder;
  color:#fff;
}

#todoform input::placeholder{
  color: #546E7A;
}

#todoform input:focus, 
#todoform button:focus{
  outline:none;
}

@media (max-width: 600px) {
  body{
    background-color: #37474F;
  }
  
  .App{
    background-color: #263238;
    min-height:600px;
    width:100%;
    margin:23px auto;
    /* border-radius: 4px; */
  }
  
  #todoform input{
    background-color: #ECEFF1;
    border:0;
    width:82%;
    height:65px;
    padding: 0 20px;
    margin:30px;
    font-size:20px;
    /* border-radius: 4px; */
    color: #263238;
  }
  
  #todoform button{
    height:70px;
    width:22%;
    /* border-radius: 5px; */
    border:0;
    background-color: #34ace0;
    cursor:pointer;
    font-weight: bolder;
    color:#fff;
  }
  
  #todoform input::placeholder{
    color: #546E7A;
  }
  
  #todoform input:focus, 
  #todoform button:focus{
    outline:none;
  }
}
